import Vue from 'vue'
import Router from 'vue-router'

const Hello = () => import('@/components/Hello')
const Index = () => import('@/views/Index')
const Iframe = () => import('@/views/Iframe')
const IframePage = () => import('@/views/IframePage')
const Home = () => import('@/views/Home')
const Website = () => import('@/views/Website')
const CreateWebsite = () => import('@/views/CreateWebsite')
const Build = () => import('@/views/Build')
const Page = () => import('@/views/Page')
const HomePage = () => import('@/views/HomePage')
const Settings = () => import('@/views/Settings')
const Block = () => import('@/views/Block')

Vue.use(Router)

export default new Router({
  // mode: 'history',
  linkExactActiveClass: 'is-active',
  linkActiveClass: 'is-active',

  routes: [
    {
      path: '/',
      component: Index,
      children: [
        {
          path: '',
          component: Home
        },
        {
          path: 'websites/create',
          component: CreateWebsite
        },
        {
          path: 'websites/:websiteIndex',
          component: Website,
          children: [
            { 
              path: 'build', 
              redirect: 'build/pages/' + data.settings.homePage
            },
            {
              path: 'build/pages/:pageIndex',
              component: Build,
              children: [
                {
                  path: '',
                  component: Page,
                },
                {
                  path: 'blocks/:blockIndex',
                  component: Block,
                }
              ]
            },
            {
              path: 'settings',
              component: Settings,
              name: 'settings'
            },            
          ]
        }
      ]
    },
    {
      path: '/iframe',
      component: Iframe,
      children: [
        {
          path: 'pages/:pageIndex',
          component: IframePage,
        }
      ]
    }
  ]
})



